.clearfix::after {
	content: "";
	display: table;
	clear: both; }

header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	.logo {
		background: #151a1f; }
	nav {
		ul {
			li {
				float: left;
				a {
					text-decoration: none;
					text-transform: uppercase;
					color: #151a1f;
					font-family: 'Roboto';
					font-weight: 700; } } } }
	.feedback {
		display: flex;
		align-items: center;
		.numbers {
			.phone_img {
				float: left; }
			.phones_number {
				float: left;
				a {
					display: block; } } } } }



